<template>
  <div
    class="slider"
  >
    <div
      v-for="item in data"
      :key="item.key"
    >
      <div
        class="slider__item"
      >
        <slot
          name="option"
          :is-active="activeModel === item.key"
          :item="item"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { useModel } from '@/utils/useModel'

export default {
  name: 'Slider',
  emits: ['update:active'],
  props: {
    data: {
      type: Array,
      default: () => []
    },
    active: {
      default: null
    }
  },
  setup (props, { emit }) {
    const { value: activeModel } = useModel(props, emit, {
      prop: 'active'
    })

    return {
      activeModel
    }
  }
}
</script>

<style lang="scss">
.slider {
  display: flex;

  &__container {
  }

  &__item {
  }
}
</style>
